import React, { useState, useEffect } from "react";
import { Grid, Stack } from "@mui/material";
import axios from "axios";
import { useParams } from "react-router";
import JsonView from "./JsonView";
import RequestTable from "./RequestTable";
import GithubTable from "./GithubTable";

const Bin = () => {
  const { binId } = useParams();
  const [requests, setRequests] = useState([]);
  const [githubs, setGithubs] = useState([]);

  const [selectedRequest, setSelectedRequest] = useState(null);
  const [selectedGithub, setSelectedGithub] = useState(null);

  const isDevelopment = process.env.NODE_ENV === "development";
  const baseUrl = isDevelopment ? "http://localhost:3001" : "";

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get(`${baseUrl}/api/bin/${binId}`);

        if (response.data && typeof response.data === "object") {
          setRequests(response.data.requests);
          setGithubs(response.data.githubs);
          console.log(response.data);
        }
      } catch (error) {
        console.log("No data yet in the bin");
        setRequests([]);
        setGithubs([]);
      }
    };
    fetchData();
  }, [binId]);

  const deleteRequest = async id => {
    try {
      await axios.delete(`${baseUrl}/api/bin/${binId}/${id}`);
      setRequests(requests.filter(request => request._id !== id)); // Update requests state
    } catch (error) {
      console.error("Error deleting request:", error);
    }
  };

  return (
    <Grid container>
      <Grid item xs={12} md={6}>
        <Stack spacing={2}>
          <RequestTable
            requests={requests}
            selectedRequest={selectedRequest}
            setSelectedRequest={setSelectedRequest}
            deleteRequest={deleteRequest}
          />
          <GithubTable
            githubs={githubs}
            selectedGithub={selectedGithub}
            setSelectedGithub={setSelectedGithub}
          />
        </Stack>
      </Grid>
      <Grid item xs={12} md={6}>
        <JsonView selectedRequest={selectedRequest} selectedGithub={selectedGithub} />
      </Grid>
    </Grid>
  );
};

export default Bin;
