import { useContext, useState, useEffect } from "react";
import { Button, Grid, Stack, Typography, styled, keyframes, Box } from "@mui/material";
import { BinContext } from "../components/BinContext";
import { useNavigate } from "react-router-dom";
import bigDonut from "../assets/bigDonut.png";
import axios from "axios";

const blink = keyframes`
  0% {opacity: 1;}
  50% {opacity: 0;}
  100% {opacity: 1;}
`;

const donutSpin = keyframes`
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(1turn);
  }
`;

const donutExit = keyframes`
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(200vw);
  }
`;

const StyledButton = styled(Button)`
  &:hover {
    background-color: transparent;
  }
`;

const StyledTypography = styled(Typography)`
  font-family: "Comic Sans MS", cursive, sans-serif;
  color: #a34a28;
`;

const StyledBlinkTypography = styled(Typography)`
  font-family: "Comic Sans MS", cursive, sans-serif;
  color: #a34a28;
  animation: ${blink} 1s infinite;
`;

const LandingPage = () => {
  const { setBinId } = useContext(BinContext);
  const navigate = useNavigate();
  const [animationRunning, setAnimationRunning] = useState(false);
  const [animationStage, setAnimationStage] = useState(0);
  const isDevelopment = process.env.NODE_ENV === "development";
  const baseUrl = isDevelopment ? "http://localhost:3001" : "";

  const handleClick = () => {
    setAnimationRunning(true);
  };

  const createBin = async () => {
    try {
      const response = await axios.post(`${baseUrl}/api/bin/new`);
      setBinId(response.data.endpoint_url);
      setAnimationStage(1);
      setTimeout(() => {
        setAnimationStage(2);
        navigate(`/bin/${response.data.endpoint_url}`);
      }, 2000);
    } catch (error) {
      console.error(error);
    }
  };

  useEffect(() => {
    if (animationRunning) {
      createBin();
    }
  }, [animationRunning]);

  return (
    <Stack className="LandingPage" sx={{ mt: "20px" }}>
      <StyledTypography variant="h4" align="center" gutterBottom>
        Welcome to SnackBin! Click on the donut to create a bin!
      </StyledTypography>

      <Grid container spacing={3} sx={{ justifyContent: "center", mt: "10%" }}>
        <StyledButton onClick={handleClick}>
          <img
            src={bigDonut}
            alt="Donut"
            width="50%"
            style={{
              animation:
                animationStage === 1
                  ? `${donutSpin} 2s linear both`
                  : animationStage === 2
                  ? `${donutExit} 2s linear forwards`
                  : "none"
            }}
          />
        </StyledButton>
      </Grid>

      {animationRunning && (
        <Box sx={{ mt: 2, textAlign: "center" }}>
          <StyledBlinkTypography variant="h5">Creating your bin...</StyledBlinkTypography>
        </Box>
      )}
    </Stack>
  );
};

export default LandingPage;
